html {
  height: 100%;
}

body {
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes {
  content: "";
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite nev;
  display: block;
  position: absolute;
  top: calc(50% - 20vmin);
  left: calc(50% - 1.5vmin);
}

.stopwatch__minutes--speed-up {
  animation-duration: 600s;
}

.stopwatch__sekundes {
  content: "";
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite nev;
  display: block;
  position: absolute;
  top: calc(50% - 38vmin);
  left: calc(50% - 1vmin);
  transform: rotate(1deg);
}

.stopwatch__sekundes--speed-up {
  animation-duration: 10s;
}

.stopwatch__center {
  content: "";
  border: 3vmin solid #f6a603;
  border-radius: 50%;
  display: block;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

@keyframes nev {
  100% {
    transform: rotate(360deg);
  }
}
